@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body{margin:0;box-sizing:border-box;height:100vh;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.grid{width:calc(1.6 * 49%);height:calc(1.6 * 19vw);display:flex;flex-wrap:wrap;flex-direction:row;box-shadow:inset 0px 0px 0px 2px black}

.node{height:1.6vw;width:1.6vw;box-shadow:inset 0px 0px 0px 1px black;display:flex;justify-content:center;align-items:center}.wall{background-image:linear-gradient(#036, #036)}.visited{background-image:linear-gradient(#adb5bd, #dee2e6)}.node-animation{-webkit-animation-name:createNode;animation-name:createNode;-webkit-animation-duration:0.3s;animation-duration:0.3s;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;background-position:50% 50%;background-repeat:no-repeat}.path{background-image:linear-gradient(#006700, #70e000)}@media only screen and (min-width: 1800px){.icon{font-size:24px}}@media only screen and (min-width: 1400px) and (max-width: 1700px){.icon{font-size:20px}}@media only screen and (max-width: 1200px){.icon{font-size:14px}}@-webkit-keyframes createNode{0%{background-size:0% 0%}100%{background-size:100% 100%}}@keyframes createNode{0%{background-size:0% 0%}100%{background-size:100% 100%}}

.controls{width:100%}#toolbar{padding:0;min-height:48px}

.container{height:100vh;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}

